import React from 'react'; 
import ButtonsBottom from '../components/ButtonsBottom';
import DynamicText from '../components/DynamicText';
import Mouse from '../components/Mouse';
import Navigation from '../components/Navigation';
import SocialNetwork from '../components/SocialNetwork';
//motion = determine au niveau de balises ce qui est animé
import {motion} from "framer-motion";
import disco from  './boule-disco.svg';
import Logo from '../components/Logo';

 const Home = () => {

    //trois variables stocke dans lobjet variants (puis on l'attribut)
    const variants = {
        initial : {
            opacity : 0,
            transition : { duration: 0.5},
            x: 100,
        },
        visible: {
            opacity:1,
            x:0,
        },
        exit: {
            opacity: 0,
            transition: { duration: 0.3},
            x: -100
        }
    }

  return (
    <main>
        <Mouse/>
        <motion.div
         className="home"
         initial="initial"
         animate="visible"
         exit="exit"
         variants= {variants}
         > 
        <Navigation/>
        <img src={disco}/>
        <Logo/>

        <SocialNetwork/>
        <div className="home-main">
            <div className="main-content"> 
                <motion.h1 drag OnDrag dragConstraints={{left:-250,
                right:950, top:-200, bottom: 250}} >COLLECTION OF  </motion.h1>
                     <motion.h2 drag OnDragEnd><DynamicText />
                      </motion.h2>
             </div>
          </div>
            <ButtonsBottom right={"/project-1"}/>

        </motion.div >
    </main>
  );
};

export default Home;