import React, { useEffect } from 'react';
import { Redirect, Route, Switch, useLocation, useHistory } from
  'react-router-dom';
import Contact from './pages/Contact';
import Home from './pages/Home';
import { Project1, Project2, Project3, Project4 } from './pages/Projects';
//pour les annimations entre les pages : framer (+lien package.json)
//AnimatePresence = où on appelle les anim 
import {AnimatePresence} from "framer-motion";


const App = () => {
  // uselocation est un hook de react router dom
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    //on relance le useeffect à chaque fois que 
    //history bouge et seulement

    //il a en parametre levent du scroll (e)
    const handleScrollToElement = (e) => {
      //wheelDeltaY = move de la souris sur axe des Y
      //ça correspond à la page localhost3001 
      const url = window.location.origin + "/";

      const wheelRouter = (after, before) => {
        // si wheeldeltaY est inf a zero, on declenche
        // un history qui push le chemin passé en para (= after)
        if (e.wheelDeltaY < 0) {
          setTimeout(() => {
            history.push(after)
          }, 500);
          // en arrière
        } else if (e.wheelDeltaY > 0) {
          setTimeout(() => {
            history.push(before)
          }, 500);
         }
      }

      //  exacte chemin sur lequel on se trouve à linstant T
      switch (window.location.href.toString()) {
        //url = localhost3000
        case url:
          if (e.wheelDeltaY < 0) {
            //settimeout pour ralentir le scroll
            setTimeout(() => {
              history.push("project-1");
            }, 500);
          }
          break;
        case url + "project-1":
          wheelRouter("project-2", "")
          break;
        case url + "project-2":
          wheelRouter("project-3", "project-1")
          break;
        case url + "project-3":
          wheelRouter("project-4", "project-2")
          break;
        case url + "project-4":
          wheelRouter("contact", "project-3")
          break;
        case url + "contact":
          if (e.wheelDeltaY > 0) {
            setTimeout(() => {
              history.push("project-4")
            }, 500);
           }
          break;
        default:
          console.log('nothing');
      }

    };
    //wheel = molette
    //history permet dinjecter une route, assure la nav sans cliquer sur des choses
    window.addEventListener('wheel', handleScrollToElement)
  }, [history])



  return (
    //pour utiliser les donnees de navigation= Switch location={location} key={location.pathname}>
  <AnimatePresence>
    <Switch location={location} key={location.pathname}>
      <Route exact path="/" component={Home} />
      <Route exact path="/project-1" component={Project1} />
      <Route exact path="/project-2" component={Project2} />
      <Route exact path="/project-3" component={Project3} />
      <Route exact path="/project-4" component={Project4} />
      <Route exact path="/contact" component={Contact} />
      <Redirect to="/" />
    </Switch>
    </AnimatePresence>
  );
};

export default App;