import React from 'react'; 

 const SocialNetwork = () => {

    // declencher une anim qd la souris passe par dessus 
    const anim = () => {
       // console.log('yes');
        // la logique des RS
        let navLinks = document.querySelectorAll(' .social-network a');
        // qua chacun soit anime indiv vers la souris
        // X et Y = evenent de la souris quon recup dans le e 

        navLinks.forEach(link => {
            link.addEventListener('mouseover', (e) => {  
                let attrX = e.offsetX - 20;
                let attrY = e.offsetY - 13; 

                link.style.transform = 'translate(${attrX}px, ${attrY}px)';
        })
        link.addEventListener('mouseleave', () => {
            link.style.transform = '';
            // reviennent a leur place
        })
    })

    }

  return (
    <div className="social-network">
    <ul className="content">

    <a href="https://vk.com/" target="_blank"
    rel="noopener noreferrer" className="hover" onMouseOver={anim}> 
    <li><i className= "fab fa-vk"></i></li></a>
    
    
    <a href="https://www.twitter.com" target="_blank"
    rel="noopener noreferrer" className="hover"  onMouseOver={anim}>   
    <li><i className= "fab fa-twitter"></i></li></a>
    
    <a href="https://www.instagram.com" target="_blank"
    rel="noopener noreferrer" className="hover"  onMouseOver={anim}> 
    <li><i className= "fab fa-instagram"></i></li></a>
    



        </ul>

    </div>
  );
};

export default SocialNetwork;