import React, { useEffect } from 'react'; 
 
 const Mouse = () => {

     useEffect(() => {
        const cursor = document.querySelector('.cursor')

         const handleCursor = (e) => {
            // au move, on declenche un top et un left (comme c en absolute ça se cale en 0)
            //e.pageY détermine où est la souris,  détermine dans le sens de la hauteur 
            cursor.style.top = e.pageY +
           "px";
            cursor.style.left = e.pageX +
            "px";
        };

        window.addEventListener('mousemove', handleCursor);

        const handleHover = () => {
            //classList.add('hovered')= correspond à un cercle plus petit
            cursor.classList.add('hovered');
        };

        const handleLeave = () => {
            //classList.add('hovered')= correspond à un cercle plus petit
            cursor.style.transition = " .3s ease-out";
            cursor.classList.remove('hovered');  
        }

        document.querySelectorAll('.hover').forEach((link) => {
             link.addEventListener('mouseover', handleHover);
             link.addEventListener('mouseleave', handleLeave);
         })

     },  []);

  return  <span className="cursor"></span> 
};

export default Mouse;

 