import React, {useState} from 'react';
import { projectsData } from '../data/projectsData';
import {motion} from "framer-motion";

const Project = (props) => {
    const [currentProject] = useState(projectsData);
    const project = currentProject[props.projectNumber]

    //random backgroung circle
    //math floor = pour arrondir la valeur
    
    let left = Math.floor(Math.random() * 200 + 700) + "px";
    let top = Math.floor(Math.random() * 200 + 750) + "px";
    let transform = "scale( " + (Math.random() + 0.7) + ")";
   
    //trois variables stocke dans lobjet variants (puis on l'attribut)
   const variants = {
    initial : {
        opacity : 0,
        transition : { duration: 0.5},
        x: 200,
    },
    visible: {
        opacity:1,
        x:0,
    },
    exit: {
        opacity: 0.4,
        transition: { duration: 0.35},
        x: -800
    }
}

//60% du temps l'img vient de la droite
//si tu trouves un resulat entre 0 et 1 supérieur à 0.4,
// alors ternaire tu fais 1 , sinn -1 
//pas d'anim de depart, i lprend celui de son parent
let plusMinus = Math.random()> 0.4 ? 1 : -1; 

let imgX = Math.random() * 350 * plusMinus;
let imgY = Math.random() * 120 * plusMinus;

const imgAnim = {
    initial: {
        opacity: 0,
        x: imgX,
        y: imgY,
    },
    visible: {
        opacity:1,
        x:0,
        y:0
    }
}

const transition = {
    ease:  [0.03, 0.87, 0.73, 0.9 ],
    duration:0.6
}


    return ( 
         <motion.div 
         className="project-main"
         initial="initial"
         animate="visible"
         exit="exit"
         variants= {variants}
         transition={transition}
         >

         <div className="project-content">
            <h1>{project.title}</h1>
            <p>{project.date}</p>
          
         </div>
         <motion.div 
                className="img-content" 
                initial="initial"
                animate="visible"
                exit="exit"
                variants= {imgAnim} 
                  >
         
         

         
             <div className="img-container hover">
                <span>
                    {/* <h3>{project.title}</h3> */}
                    <p>{project.infos}</p>
                </span>
                <img src={project.img} alt={project.title} />  
            </div>
             
         </motion.div>    
     </motion.div>
        
    );
};

export default Project;